.co-container {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 50px;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    background-color: #0000001e;
    font-family: 'Nunito', sans-serif;
}

.co-title {
    text-align: center;
    margin-bottom: 30px;
    font-size: 20px;
    font-family: 'Nunito', sans-serif;
}

.co-container form {
    width: 350px;
    background-color: #ffffff;
    padding: 30px 10px;
    border-radius: 10px;
    position: relative;
    display: flex;
    flex-direction: column;
}

.co-container form .form-group {
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
}
.labels{
    letter-spacing: 1.5px;
    font-family: 'Nunito', sans-serif;
    font-weight: 500;

}

.co-container form .form-group label {
    margin-bottom: 5px;
}

.co-container form .form-group input {
    height: 30px;
    border: 1px solid #2f5f74;
    padding: 5px 15px;
    outline: none;
    color: #272727;
    font-family: 'Nunito', sans-serif;
    border-radius: 5px;
}

.close-form {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    right: -15px;
    top: -15px;
    width: 35px;
    height: 35px;
    background-color: #ca2734;
    color: #ffffff;
    font-weight: bold;
    border-radius: 50%;
    font-size: 20px;
    cursor: pointer;
}

.co-btn button {
    background-color: #342ac2;
    color: #ffffff;
    padding: 5px 15px;
    border: 1px solid transparent;
    text-transform: uppercase;
    border-radius: 4px;
    display: block;
    margin: 0 auto;
    transition: all 0.5s;
}

.co-btn button:hover {
    background-color: #ffffff;
    color: #000000;
    border: 1px solid #342ac2;
}